body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#lp-base-container{
  height:100vh;
  width:100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

#lp-fields-container{
  height:250px;
  width:250px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  input{
    height:50px;
    width:90%;
  }

  button{
    margin:24px;
    width:50%;
    height:40px;
    cursor: pointer;
  }
}

.full-vh{
  height:100vh;
}

.d-flex{
  display: flex;
}

.p-40{
  padding:40px;
}

.color-blue{
  color:blue;
}

.color-red{
  color:red;
}

.w-50{
  width:50%;
}